.modalClass {
    background-color: rgba(0,0,0,0.5);
    position: fixed; 
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  /* position: fixed; is positioned relative to the viewport, 
  which means it always stays in the same place even if the page is scrolled */

